@import ../../sass/index

.nav
    display: flex
    &._header 
        gap: min(20px, 4vw)
        margin-left: auto
        font-size: min(20px, 5vw)
        align-items: center
        color: var(--text-color)
        @media screen and (max-width: 1000px)
            display: none
    &._modal
        flex-direction: column
        align-items: center

